export const loginIntoApp = () => {
  return axios
    .post("http://35.200.162.143:8080/authenticate", {
      username: "admin",
      password: "admin@1234",
    })
    .then((response) => {
      console.log(response);
      localStorage.setItem("access_token", response.data.token);
      return;
    });
};

